import React from 'react';
import { DatePicker, Button, Tooltip, Layout, Carousel, Row, Col, Tag, Badge, Avatar, Card } from 'antd';
import Data from "../data/resume-data.json"

const { Header, Footer, Sider, Content } = Layout;

function Carreira() {
    return (

        <Content className="padding-content">
            <Row>
                {

                    Data.map(item => (

                        <Col key={item.id} xs={24} sm={24} md={12} lg={12}>
                            <Card title={item.titulo} bordered={false} style={{ margin: 5 }}>
                                <Row>
                                    <Col className="padding-content" span={24}><span style={{ fontWeight: 'bold' }}>Cargo:</span> {item.cargo} <br /> <Tag color="gray">{item.grau}</Tag>{item.atual == true ? (<Tag color="green">Atualmente</Tag>) : null}</Col>
                                </Row>
                                <Row>
                                    <Col className="padding-content" span={24}><span style={{ fontWeight: 'bold' }}>Descricao:</span><br />{item.descricao}</Col>
                                </Row>
                                <Row>
                                    <Col className="padding-content" span={24}><span style={{ fontWeight: 'bold' }}>Projetos:</span></Col>
                                </Row>
                                <Row className="content-carreira">

                                    {
                                        item.projetos.map(projeto => (

                                            <Row>
                                                <Col className="padding-content">
                                                    <Row>
                                                        <span className="titulo-carreira">Descrição:</span>
                                                    </Row>
                                                    <Row>
                                                        <span className="descricao-carreira-item">{projeto.titulo}</span>
                                                    </Row>
                                                    <Row>
                                                        <span className="titulo-carreira">Stack:</span>
                                                    </Row>
                                                    <Row>
                                                        <span className="stack-carreira-item">{projeto.stack}</span>
                                                    </Row>
                                                </Col>
                                            </Row>

                                        ))
                                    }

                                </Row>
                            </Card>
                        </Col>


                    ))
                }
            </Row>
        </Content>

    );
}
export default Carreira;
